import { useState, useEffect } from 'react';

import request from '../request';
export default function useGroup() {
 const [loading, setLoading] = useState(false);
 const [data, setData] = useState(null);
 const [error, setError] = useState(null);
 useEffect(() => {
  if (error) {
   alert(error);
  }
 }, [error]);
 useEffect(() => {
  if (data) {
   console.log('Add group success');
  }
 }, [data]);
 const resetState = () => {
  setError(null);
  setData(null);
  setLoading(false);
 };
 const createGroup = async (data) => {
  resetState();
  try {
   const resp = await request.post('/group', data);
   setData(resp.data);
   console.log({ resp });
  } catch (error) {
   console.log('add error', error);
   setData(null);
   setError(error);
  } finally {
   setLoading(false);
  }
 };

 return {
  createGroup,
  loading,
  data,
  error
 };
}
