// import React from 'react';
import ReactDOM from 'react-dom';
import { Route, Routes, HashRouter } from 'react-router-dom';
import './style.css';
import App from './App';
// import Welcome from './routes/Welcome'
import Login from './routes/Login';
import Main from './routes/Main';
import Channels from './routes/Channels';
import Contacts from './routes/Contacts';
import ContactChat from './routes/ContactChat';
ReactDOM.render(
 <HashRouter>
  <Routes>
   <Route path="/" element={<App />}>
    <Route index element={<Main />} />
    <Route path="channel" element={<Main />}>
     <Route index element={<Channels />} />
     <Route path=":cid" element={<Channels />} />
    </Route>
    <Route path="contacts" element={<Main />}>
     <Route index element={<Contacts />} />
     <Route path=":uid" element={<Contacts />}>
      <Route index element={<ContactChat />} />
     </Route>
    </Route>
    <Route path="dms" element={<Main />}>
     <Route index element={null} />
    </Route>
    <Route
     path="*"
     element={
      <main style={{ padding: '1rem' }}>
       <p>There's nothing here!</p>
      </main>
     }
    />
   </Route>
   <Route path="/login" element={<Login />} />
  </Routes>
 </HashRouter>,
 document.getElementById('root')
);
