import { useEffect, useState } from 'react';
import TextareaAutosize from 'react-textarea-autosize';
import styled from 'styled-components';
import { useParams, useOutletContext } from 'react-router-dom';
import { RiAddFill } from 'react-icons/ri';
// import { CgHashtag } from 'react-icons/cg'
import useUser from '../service/hooks/useUser';
import useUserEvents from '../service/hooks/useUserEvents';
import useMessage from '../service/hooks/useMessage';
const StyledWrapper = styled.section`
 display: flex;
 flex-direction: column;
 justify-content: flex-start;
 width: 100%;
 > .head {
  background: #fff;
  padding: 18px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: 0px 1px 0px rgba(0, 0, 0, 0.1);
  .name {
   display: flex;
   align-items: center;
   gap: 10px;
   color: #3a3a3a;
   font-weight: 600;
   font-size: 14px;
   line-height: 20px;
  }
 }
 .chat {
  height: 100%;
  position: relative;
  background: #fff;
  /* max-width: 716px; */
  margin-top: 1px;
  padding: 16px;
  min-height: 400px;
  overflow-y: scroll;
  .msgs {
   display: flex;
   flex-direction: column;
   .msg {
    position: relative;
    margin-top: 28px;
    padding-top: 30px;
    display: flex;
    align-items: flex-start;
    gap: 16px;
    &:before,
    &:after {
     content: '';
     position: absolute;
     top: 0;
    }
    &:before {
     height: 1px;
     background-color: #e3e5e8;
     width: 100%;
     left: 0;
    }
    &:after {
     padding: 2px 4px;
     color: #78787c;
     font-weight: 600;
     font-size: 12px;
     line-height: 18px;
     content: attr(data-time);
     position: absolute;
     left: 50%;
     transform: translateX(-50%) translateY(-50%);
     background-color: #fff;
    }
    .head {
     border-radius: 50%;
     width: 40px;
     height: 40px;
     img {
      width: 100%;
      height: 100%;
     }
    }
    .body {
     display: flex;
     flex-direction: column;
     gap: 8px;
     .meta {
      display: flex;
      align-items: center;
      gap: 8px;
      .name {
       color: #06b6d4;
       font-weight: 500;
       font-size: 14px;
       line-height: 20px;
      }
      .time {
       color: #bfbfbf;
       font-weight: 500;
       font-size: 12px;
       line-height: 18px;
      }
     }
     .content {
      color: #374151;
      line-height: 20px;
     }
    }
   }
  }
  .send {
   position: absolute;
   bottom: 10px;
   left: 50%;
   border-radius: 8px;
   width: calc(100% - 32px);
   transform: translateX(-50%);
   padding: 16px;
   background: #f5f6f7;
   display: flex;
   justify-content: space-between;
   align-items: center;
   gap: 18px;
   .input {
    background: inherit;
    width: 100%;
    display: flex;
    align-items: center;
    textarea {
     padding: 3px 5px;
     font-weight: 500;
     font-size: 14px;
     line-height: 20px;
     color: #616161;
     width: 100%;
     /* outline: none; */
     background: none;
     /* &:focus{
                            border: 1px solid #eee;
                        } */
    }
   }
  }
 }
`;
export default function ContactChat() {
 const users = useOutletContext();
 const [msgs, setMsgs] = useState([]);
 const { uid } = useParams();
 const { addEventlistener } = useUserEvents();
 const { loadUser, data, loading } = useUser();
 const { sendMessage, sending } = useMessage();
 const [msg, setMsg] = useState('');
 console.log({ uid });
 useEffect(() => {
  addEventlistener('chat', (data) => {
   console.log('chat', { data });
   setMsgs((prevs) => {
    return [...prevs, data];
   });
  });
 }, []);
 useEffect(() => {
  if (uid) {
   loadUser(uid);
  }
 }, [uid]);
 const handleInputChange = (evt) => {
  setMsg(evt.target.value);
 };
 const handleSendMessage = async () => {
  const mid = await sendMessage({ to: uid, content: msg });
  if (mid) {
   const currentUid = JSON.parse(localStorage.getItem('USER'))?.uid;
   setMsgs((prevs) => {
    const sendedMsg = {
     mid,
     content: msg,
     from_user: currentUid,
     created_at: new Date().getTime()
    };
    return [...prevs, sendedMsg];
   });
   setMsg('');
  }
 };
 console.log({ users });
 if (loading) return 'loading';
 if (!data) return null;
 return (
  <StyledWrapper className="col">
   <div className="head">
    <div className="name">{data.name}</div>
    <div className="memebers">members</div>
   </div>
   <div className="chat">
    <div className="msgs">
     {msgs.map((m) => {
      const { mid, content, from_user, created_at } = m;
      const sender = users.find((u) => u.uid == from_user) || {};
      return (
       <div key={mid} className="msg" data-time="2021/09/08">
        <div className="head">
         <img src="https://static.nicegoodthings.com/project/ext/webrowse.logo.png" alt="header" />
        </div>
        <div className="body">
         <div className="meta">
          <span className="name">{sender.name}</span>
          <span className="time">{new Date(created_at).toLocaleString()}</span>
         </div>
         <div className="content">
          <p>{content}</p>
         </div>
        </div>
       </div>
      );
     })}
    </div>
    <div className="send">
     <div className="add">
      <RiAddFill size={18} color="#78787C" />
     </div>
     <div className="input relative">
      <TextareaAutosize
       maxRows={8}
       minRows={1}
       onChange={handleInputChange}
       value={msg}
       placeholder={` ${data.name} `}
      />
      <button
       disabled={sending}
       className="btn p-2 rounded bg-green-500 absolute right-1 top-[50%] translate-y-[-50%] text-white"
       onClick={handleSendMessage}
      >
       {sending ? `Sending` : `Send`}
      </button>
     </div>
     <div className="emoji">emoji</div>
    </div>
   </div>
  </StyledWrapper>
 );
}
