// import { message } from 'antd';
import { useState, useEffect } from 'react';
// import request from '../request';
import { BASE_URL, TOKEN } from '../config';
export default function useUserEvents() {
 const [loading, setLoading] = useState(false);
 // const [data, setData] = useState(null);
 const [error, setError] = useState(null);
 const callBacks = {};
 useEffect(() => {
  if (error) {
   alert(JSON.stringify(error));
  }
 }, [error]);
 useEffect(() => {
  if (window.CHAT_EVENTS) {
   window.CHAT_EVENTS.onmessage = (evt) => {
    try {
     const data = JSON.parse(evt.data);
     switch (data.type) {
      case 'heartbeat':
       {
        // to do
       }
       break;

      default:
       {
        if (callBacks[data.type]) {
         const _data = { ...data.payload, mid: data.mid };
         callBacks[data.type].call(null, _data);
        }
        console.log('sse msg', data);
       }
       break;
     }
    } catch (error) {
     console.log(error);
    }
   };
   return;
  }
  setLoading(true);
  window.CHAT_EVENTS = new EventSource(
   `${BASE_URL}/user/events?api-key=${localStorage.getItem(TOKEN)}`
  );
  window.CHAT_EVENTS.onopen = () => {
   setLoading(false);
  };
  window.CHAT_EVENTS.onerror = (err) => {
   setError(err);
   setLoading(false);
  };
 }, []);
 const addEventlistener = (evt = '', callback) => {
  if (!evt) return;
  callBacks[evt] = callback;
 };
 return {
  addEventlistener,
  loading,
  // data,
  error
 };
}
