// import React from 'react';
import { NavLink, Outlet } from 'react-router-dom';
import styled from 'styled-components';
import useUserList from '../service/hooks/useUserList';
// import { VscTriangleDown } from 'react-icons/vsc'
// import { RiAddFill } from 'react-icons/ri'
// import { CgHashtag } from 'react-icons/cg'
const StyledWrapper = styled.div`
 display: flex;
 flex-direction: column;
 justify-content: flex-start;
 height: 100vh;
 width: 260px;
 box-shadow: inset -1px 0px 0px rgba(0, 0, 0, 0.1);
 background: #f5f6f7;
.items {
   display: flex;
   flex-direction: column;
   gap: 1px;
   .item {
    cursor: pointer;
    margin-right: 1px;
    padding: 8px;
    .nav {
      padding:8px;
     display: flex;
     align-items: center;
     gap: 8px;
     font-weight: 600;
     font-size: 16px;
     line-height: 24px;
     color: #52525b;
     border-radius: 4px;
     &:hover{
       background: rgba(116, 127, 141, 0.2);
     }
     img {
      line-height: 1;
      font-size: 10px;
      width: 32px;
      height: 32px;
      border-radius: 50%;
     }
    }
 }
}
`;
export default function Contacts() {
  const { data, loading } = useUserList();
  if (loading) return <StyledWrapper>loading</StyledWrapper>;
  if (!data) return null;
  return (
    <>
      <StyledWrapper>
        <ul className="items">
          {data.map(({ uid, name, avatar }) => {
            return (
              <li className="item" key={uid}>
                <NavLink className={'nav'} to={`/contacts/${uid}`}>
                  <img src={avatar} alt="avatar" />
                  <span>{name}</span>
                </NavLink>
              </li>
            );
          })}
        </ul>
      </StyledWrapper>
      <Outlet context={data} />
    </>
  );
}
