import { useState } from 'react';
import request from '../request';
// import { TOKEN, TOKEN_HEADER, BASE_URL } from '../config'
export default function useMessage() {
 const [sending, setSending] = useState(false);
 // const [data, setData] = useState(null);
 const [error, setError] = useState(null);
 const sendMessage = async ({ to = null, content = null, type = 'text/plain' }) => {
  try {
   setError(null);
   setSending(true);
   const resp = await request.post(`/user/${to}/send`, content, {
    headers: {
     'Content-Type': type
    }
   });
   console.log('send resp', resp);
   setSending(false);
   return resp?.data;
  } catch (error) {
   setSending(false);
   setError(error);
   return null;
  }
 };
 return {
  sendMessage,
  sending,
  error
 };
}
